@media (max-width: 1000px) {
    .navbar {
        display: none;
    }
    .navbar-mobile {
        position: relative;
        z-index: 1001; /* Garante que o ícone do menu fique acima da sobreposição */
    }

    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.5); /* Fundo branco com opacidade de 0.5 */
        z-index: 1000; /* Garante que a sobreposição fique acima de todos os elementos, exceto o ícone do menu */
        display: none; /* Inicialmente escondido */
    }

    .overlay.active {
        display: block;
    }
}